import React from "react";
import "./about-styles.css";

const About = () => {
  return (
    <div className="about-container">
      <div className="about-heading">
        <h1>About</h1>
        <h3>MY BACKGROUND</h3>
      </div>
      <div className="about-para">
        <p>
          With a passion for business, social media, and all things marketing, I
          have both the skill set and professional background necessary to dive
          deep into the marketing world. As an upbeat, self-motivated team
          player with excellent communication, I envision an exciting future in
          the industry. Browse my site to see all that I have to offer.
        </p>
      </div>
    </div>
  );
};

export default About;
